@mixin xl-width {
  @media screen and (max-width: $xl-width) {
      @content;
    }
}
@mixin lg-width {
  @media screen and (max-width: $lg-width) {
    @content;
  }
}
@mixin md-width {
  @media screen and (max-width: $md-width) {
    @content;
  }
}

@mixin sm-width {
  @media screen and (max-width: $sm-width) {
    @content;
  }
}
@mixin xs-width {
  @media screen and (max-width: $xs-width) {
    @content;
  }
}
